/* General Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.resume-container {
  width: 80%;
  max-width: 900px;
  margin: 50px auto;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Header Styles */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 5px solid #ffb700;
  padding-bottom: 20px;
}

.left-header h1 {
  margin: 0;
  font-size: 2.5em;
}

.right-header p {
  margin: 5px 0;
  font-size: 1.1em;
}

/* Personal Profile */
.personal-profile {
  margin: 20px 0;
}


.personal-profile h2 {
  font-size: 1.5em;
 
  margin-bottom: 10px;
}
.personal-profile p{
  display: flex;
  flex-wrap: wrap; 

}

/* Work Experience */
.work-experience {
  margin: 30px 0;
}
.job {
  display: flex;
  flex-wrap: wrap; 
  gap: 10px; 
}
.job {
  margin-bottom: 20px;
}

.job h2 {
  font-size: 1.3em;

  margin-bottom: 5px;
}

.job p {
  font-size: 1.1em;
  color: #777;
}

/* Skills Section */
.keySkills {
  display: flex;
  flex-wrap: wrap; 
  gap: 10px; 
}

.keySkills div {
  flex: 1 1 calc(33.33% - 10px); 
  padding: 5px;
  box-sizing: border-box;
  text-align: center;
}

.keySkills div p {
  margin: 0;
}

/* Education Section */


.institution {
  font-size: 1.2em;
  font-weight: bold;
  ;
}

.degree, .year, .gpa {
  font-size: 1.1em;
  ;
}

/* Footer */
footer {
  text-align: center;
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #ddd;
  color: #888;
}
